@import url(https://js.arcgis.com/4.21/@arcgis/core/assets/esri/themes/light/main.css);
.list {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    width: 300px;
    height: 400px;
}

.Home {
    display: flex;
    width: 900px;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 20px;
}

.outer {
    display:flex;
    justify-content: center;
}

.inner {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.SearchPanel {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding-top: 50px;
    height: 200px;

    

}
.searchHDB {
    display: flex;
    flex-direction: column;
    height: 100px;
    margin-top: 50px;
    align-items: center;
}

input {
    margin:15px 0;
	padding:15px 10px;
	width:100%;
	outline:none;
	border:1px solid #bbb;
	border-radius:20px;
	display:inline-block;
	box-sizing:border-box;
	   transition:0.2s ease all;
}

input[type=text]:focus,
input[type="password"]:focus {
	border-color:cornflowerblue;
}

input[type=text] {
    
    width: 30em;
    height: 20px;
}
